.tu-input-number {
  position: relative;
  display: inline-block;
  width: 100%;
  line-height: calc(32px - 2px); }
  .tu-input-number .tu-input {
    display: block; }
    .tu-input-number .tu-input__inner {
      -webkit-appearance: none;
      padding-left: calc(32px + 10px);
      padding-right: calc(32px + 10px);
      text-align: center; }
  .tu-input-number__increase, .tu-input-number__decrease {
    position: absolute;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    top: 1px;
    width: 32px;
    height: 100%;
    background: transparent;
    color: #8394be;
    cursor: pointer;
    font-size: 13px;
    transition: all 0.3s ease; }
    .tu-input-number__increase:hover, .tu-input-number__decrease:hover {
      color: #6d5dfc;
      font-weight: 600; }
    .tu-input-number__increase.is-disabled, .tu-input-number__decrease.is-disabled {
      color: #bec8e4;
      cursor: not-allowed; }
  .tu-input-number__increase {
    right: 1px; }
  .tu-input-number__decrease {
    left: 1px; }
  .tu-input-number.is-disabled .tu-input-number__increase, .tu-input-number.is-disabled .tu-input-number__decrease {
    color: #bec8e4; }
    .tu-input-number.is-disabled .tu-input-number__increase:hover, .tu-input-number.is-disabled .tu-input-number__decrease:hover {
      color: #bec8e4;
      cursor: not-allowed; }
  .tu-input-number--mini {
    line-height: calc(22px - 2px); }
    .tu-input-number--mini .tu-input-number__increase, .tu-input-number--mini .tu-input-number__decrease {
      width: 22px;
      font-size: 10px; }
      .tu-input-number--mini .tu-input-number__increase [class*="tu-icon"], .tu-input-number--mini .tu-input-number__decrease [class*="tu-icon"] {
        transform: scale(0.8); }
  .tu-input-number--small {
    line-height: calc(28px - 2px); }
    .tu-input-number--small .tu-input-number__increase, .tu-input-number--small .tu-input-number__decrease {
      width: 28px;
      font-size: 12px; }
      .tu-input-number--small .tu-input-number__increase [class*="tu-icon"], .tu-input-number--small .tu-input-number__decrease [class*="tu-icon"] {
        transform: scale(0.9); }
  .tu-input-number--large {
    line-height: calc(40px - 2px); }
    .tu-input-number--large .tu-input-number__increase, .tu-input-number--large .tu-input-number__decrease {
      width: 40px;
      font-size: 14px; }
  .tu-input-number.is-without-controls .tu-input__inner {
    padding-left: 15px;
    padding-right: 15px; }
  .tu-input-number.is-controls-right .tu-input__inner {
    padding-left: 15px;
    padding-right: calc(32px + 10px); }
  .tu-input-number.is-controls-right .tu-input-number__increase, .tu-input-number.is-controls-right .tu-input-number__decrease {
    line-height: calc((32px - 2px) / 2);
    height: calc((32px - 2px) / 2); }
    .tu-input-number.is-controls-right .tu-input-number__increase [class*="tu-icon"], .tu-input-number.is-controls-right .tu-input-number__decrease [class*="tu-icon"] {
      transform: scale(0.8); }
  .tu-input-number.is-controls-right .tu-input-number__decrease {
    right: 1px;
    bottom: 1px;
    top: auto;
    left: auto; }
